.list-variants {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 100%;
  max-height: 0;
  background: white;
  top: 100%;
  border: 1px rgba(0, 0, 0, 0.3) solid;
  border-top: 0;
  z-index: 99999;
  padding-left: 40px;
  padding-right: 15px;
  overflow-y: scroll;
  transition: max-height 1s ease-in-out;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;

  -ms-overflow-style: none; /* IE 10+ */
  scrollbar-width: none; /* Firefox */

  &::-webkit-scrollbar { /* chrome based */
    width: 0; /*  scrollbar'a */
    background: transparent; /*  */
  }

  &.filled {
    max-height: 200px;
  }

  &__item {
    margin: 5px 0;
    font-size: 12px;
    border-bottom: 1px rgba(0, 0, 0, 0.09) solid;
    font-weight: normal;
    color: $main-color;

    a {
      color: inherit;
    }
  }
}