.product-tabs {
  width: 100%;

  &.in-profile {
    margin-top: 20px;
  }

  &.in-product {
    margin-top: 70px;
  }

  ~ .tab-content {
    width: 100%;
  }

  ~ .product-tab-content {
    padding: 30px 0;
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;

    p {
      margin: 0;
      padding: 0;
    }

    ul li {
      //padding-left: 15px;
      //font-size: 14px;
      //line-height: 32px;
      //font-weight: 400;
    }

    ul li::before {
      content: "\2022";
      color: $main-color;
      font-weight: bold;
      display: inline-block;
      width: 1em;
      margin-left: -1em;
    }
  }

  .nav-tabs {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    @media (max-width: 790px) {
      flex-direction: column;
      justify-content: center;
    }
  }

  .nav-item {
    padding-top: 2px;
    padding-bottom: 2px;
  }

  .nav-item {
    text-align: center;
    width: auto;
    flex: 1 1 auto;
    font-size: 18px;
    font-weight: 700;
    color: black;
    border-color: transparent;

    &.active {
      background: #C6C6C6;
      border-color: transparent;
      color: #000;
    }

    &:hover {
      border-color: transparent;
    }
  }
}